.tweet{
  position: relative;
  width:100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  background: #fff;
  margin-bottom: 50px;
  padding:30px 20px;
  border: 10px;
  color:rgba(0,0,0,0.8);
  h4{
    font-size: 14px;
  }
  img{
    position: absolute;
    right: 5px;
    top:50%;
    transform: translateY(-50%);
    width:70px;
    height: 70px;
    border-radius: 10px;
  }
  &>span{
    color:rgba(127,210,255,0.8);
    position: absolute;
    top:-20px;
    left: 0;
    font-size: 11px;
  }

  .tweet__actions{
    position: absolute;
    bottom:-20px;
    right: 0;

    span{
      color:rgba(127,210,255,0.8);
      cursor: pointer;
      padding:0 5px;
      }
    }

    .tweet_edit{
      .formBtn{
        margin-top:15px;
        margin-bottom:5px;
        cursor: pointer;
        
      }
    }
  .tweet_edit{
    display: flex;
    flex-direction: column;
    margin-top:10px;
    img{
      position: unset;
      width:50%;
      height:50%;
      transform:translateY(0);
    margin-bottom:20px;
    }
    
    input{
      text-align: center;
    }

  }

  }


