@import url('https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css');

*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}

ul{
  list-style: none;
}

input{
  all:unset;
  box-sizing: border-box;
  appearance: none;
}

body{
  background-color: #000;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 14px;
  color:#fff;
}

button{
  background: #fff;
  color:#000
}

a{
  text-decoration: none;
  color:inherit;
}

form{
  width:100%;
}

.container{
  width: 100%;
  max-width: 320px;
  margin:20px auto 50px;  
  display: flex;
  flex-direction: column;
  align-items: center;


  &>div{
    width:100%;
  }

}

.formInput{
  width:100%;
  padding: 10px 20px;
  border-radius: 20px;
  border:1px solid #000;
  text-align: center;
  background-color: #fff;
  color:#000;
}


.formBtn{
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  background-color: #04aaff;
  color:#fff;
  border-radius: 20px;
}

.cancelBtn{
  cursor:pointer;
  background-color: tomato;
}